import { useState } from "react";
import FormModal from "./FormModal"; // Import the new component
import StuImage1 from "../assets/pic4.jpg";
import StuImage2 from "../assets/pic6.jpg";

const HeroSection = ({ activeTab }) => {
  const [formModalOpen, setFormModalOpen] = useState(false);
  const textColor = activeTab === "student" ? "text-black" : "text-black";

  // Form submission handler
  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.target;
    const formData = new FormData(form);

    // Map form fields to Google Form entry IDs
    const googleFormData = new FormData();
    googleFormData.append("entry.271377800", formData.get("name")); // Name
    googleFormData.append("entry.1059613645", formData.get("mobile")); // Mobile
    if (activeTab === "student") {
      googleFormData.append("entry.505168970", formData.get("locality")); // Locality
      googleFormData.append("entry.2115027460", formData.get("classGrade")); // Class/Grade
    } else {
      googleFormData.append("entry.1361867851", formData.get("email")); // Email
      googleFormData.append("entry.1272021577", formData.get("qualification")); // Qualification/Occupation
    }
    googleFormData.append("entry.2132025188", activeTab === "student" ? "Student" : "Tutor"); // Student/Teacher

    fetch("https://docs.google.com/forms/u/0/d/e/1FAIpQLSdx-qrrSiMnzAhTbAaxU7UJfj0aZJlyPgCl3smjVFaWh4k-xA/formResponse", {
      method: "POST",
      body: googleFormData,
      mode: "no-cors"
    })
      .then(() => {
        setFormModalOpen(true);
        form.reset();
      })
      .catch((error) => console.error("Form submission error:", error));
  };

  return (
    <section
      className="min-h-screen w-full bg-slate-950 grid place-content-center top-0 overflow-hidden relative"
      style={{
        backgroundImage: `url(${activeTab === "student" ? StuImage1 : StuImage2})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
      }}
    >
      <div className="absolute top-0 left-0 w-full h-full z-0"></div>
      <div className="absolute bottom-0 left-0 w-full h-full z-10"></div>
      <div className="relative z-20 px-4 sm:px-6 md:px-8 mx-16">
        <div className="flex flex-col lg:flex-row gap-8 items-center lg:items-start mt-16 sm:mt-0">
          <div className="text-center lg:text-left mt-20 lg:mt-16 lg:mr-10">
            <h1
              className={`text-3xl sm:text-4xl md:text-5xl lg:text-6xl 2xl:text-7xl font-bold tracking-tight leading-tight animate-fadeIn ${textColor}`}
            >
              {activeTab === "student"
                ? "Unlock Your Potential with Home Tutoring"
                : "Empower Minds as a Home Tutor"}
            </h1>
            <p
              className={`text-base sm:text-lg md:text-xl mt-2 sm:mt-4 md:mt-6 ${
                activeTab === "student" ? "text-gray-700" : "text-gray-700"
              }`}
            >
              {activeTab === "student"
                ? "Trusted by over 1M+ students for personalized academic success."
                : "Join Tutor Kendra to connect with students and share your expertise."}
            </p>
            <div className="mt-6 sm:mt-8 space-y-4">
              <div className="">
                {activeTab === "student" ? (
                  <form onSubmit={handleSubmit} className="space-y-4">
                    <input type="hidden" name="studentTeacher" value="Student" />
                    <div className="flex flex-col sm:flex-row gap-4">
                      <div className="flex-2">
                        <input
                          type="text"
                          name="name"
                          placeholder="Enter your name"
                          className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                          required
                        />
                      </div>
                      <div className="flex-1">
                        <input
                          type="text"
                          name="mobile"
                          placeholder="+91 Enter mobile number"
                          className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                          required
                        />
                      </div>
                    </div>
                    <div className="flex flex-col sm:flex-row gap-4">
                      <div className="flex-2">
                        <input
                          type="text"
                          name="locality"
                          placeholder="Enter your locality"
                          className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                          required
                        />
                      </div>
                      <div className="flex-1">
                        <input
                          type="text"
                          name="classGrade"
                          placeholder="Enter Class/Grade"
                          className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                          required
                        />
                      </div>
                    </div>
                    <button
                      type="submit"
                      className="w-full sm:w-auto px-6 py-3 mx-0 bg-blue-500 text-white rounded-3xl hover:bg-blue-600"
                    >
                      Book Now
                    </button>
                  </form>
                ) : (
                  <div className="space-y-4">
                    <form onSubmit={handleSubmit} className="space-y-4">
                      <input type="hidden" name="studentTeacher" value="Tutor" />
                      <div className="flex flex-col sm:flex-row gap-4">
                        <div className="flex-2">
                          <input
                            type="text"
                            name="name"
                            placeholder="Enter your name"
                            className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                            required
                          />
                        </div>
                        <div className="flex-1">
                          <input
                            type="text"
                            name="mobile"
                            placeholder="+91 Enter mobile number"
                            className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                            required
                          />
                        </div>
                      </div>
                      <div className="flex flex-col sm:flex-row gap-4">
                        <div className="flex-2">
                          <input
                            type="email"
                            name="email"
                            placeholder="Enter your email"
                            className="w-3xl px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-700"
                            required
                          />
                        </div>
                        <div className="flex-1">
                          <select
                            name="qualification"
                            className="min-w-[285px] px-4 py-3 rounded-3xl bg-white text-black border drop-shadow-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                            required
                          >
                            <option value="" disabled selected>Select Qualification</option>
                            <option value="10th">10th</option>
                            <option value="12th">12th</option>
                            <option value="graduation">Graduation</option>
                            <option value="post-graduation">Post-Graduation</option>
                          </select>
                        </div>
                      </div>
                      <div className="flex flex-col sm:flex-row gap-4">
                        <button
                          type="submit"
                          className="w-full sm:w-auto px-6 py-3 bg-blue-500 text-white rounded-3xl hover:bg-blue-600"
                        >
                          Register as Tutor
                        </button>
                        <a
                          href="https://whatsapp.com/channel/0029Vb9NgOKFnSz9TesD9R0q"
                          className="block text-center px-4 py-2 sm:px-6 sm:py-3 bg-green-600 text-white rounded-3xl hover:bg-green-700 transition-all max-w-xs"
                        >
                          Join WhatsApp Channel
                        </a>
                      </div>
                    </form>
                  </div>
                )}
              </div>
            </div>
          </div>

          <div className="relative flex flex-col justify-center md:justify-end lg:ml-12">
            <div className="relative w-64 h-64 sm:w-80 sm:h-72 md:w-[500px] lg:h-[400px]"></div>
            <div className={`relative mt-8 flex items-center p-4 rounded-xl ${textColor}`}></div>
          </div>
        </div>
      </div>
      <FormModal
        isOpen={formModalOpen}
        onClose={() => setFormModalOpen(false)}
        activeTab={activeTab}
      />
    </section>
  );
};

export default HeroSection;