import { ReactLenis } from 'lenis/react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import Navbar from './Navbar';
import HeroSection from './HeroSection';
import InfoSection from './InfoSection';
import BenfitSection from './BenfitSection';
import TestimonialSection from './TestimonialSection';
import CounselingSection from './CounselingSection';
import NameText from './name_text';
import Footer from './Footer';
import TermsAndConditions from './TermsAndConditions';
import LogoLoader from './LogoLoader'; // Import the new LogoLoader component

// Main App Component with Routing
export default function Index() {
  return (
    <Router>
      <ReactLenis root>
        <AppContent />
      </ReactLenis>
    </Router>
  );
}

// Separate component to handle routing and state
function AppContent() {
  const location = useLocation(); // Get current URL
  const activeTab = location.pathname === '/tutor' ? 'tutor' : 'student'; // Determine active tab from URL

  return (
    <>
      {/* Logo Loader */}
      <LogoLoader />

      {/* Render Navbar and Main content only if not on Terms and Conditions page */}
      {location.pathname !== '/terms-and-conditions' && (
        <>
          <Navbar activeTab={activeTab} />
          <main className="">
            <div className="wrapper">
              <HeroSection activeTab={activeTab} />
              <InfoSection activeTab={activeTab} />
            </div>

            <TestimonialSection activeTab={activeTab} />
            <BenfitSection activeTab={activeTab} />
            <CounselingSection activeTab={activeTab} />
            <NameText />
            <Footer />
          </main>
        </>
      )}

      {/* Define routes */}
      <Routes>
        <Route path="/student" element={null} />
        <Route path="/tutor" element={null} />
        <Route path="/terms-and-conditions" element={<TermsAndConditions />} />
        <Route path="*" element={null} /> {/* Default route */}
      </Routes>
    </>
  );
}